<template>
  <div class="addConsignee">
    <van-nav-bar
      title="新增收货人"
      @click-left="$toPage(-1)"
      @click-right="onClickCommit"
      >
      <van-icon name="arrow-left" size="24px" color="#666666" slot="left" />
      <span slot="right" style="font-size: 15px">保存</span>
    </van-nav-bar>
    <van-cell-group>
      <van-field
        v-model="username"
        label="收货人"
        maxlength="10"
        placeholder="请输入用户名"
        size="large"
      />
      <van-field
        v-model="userphone"
        size="large"
        label="手机号"
        type="tel"
        maxlength="11"
        :error-message="message"
        placeholder="请输入手机号"
        @focus="message = ''"
      />
    </van-cell-group>
    <van-cell-group>
      <van-cell
        size="large"
        clickable
        title="设为默认"
        @click="toggle"
      >
        <van-checkbox v-model="result" />
      </van-cell>
    </van-cell-group>
  </div>
</template>
<script>
export default {
  data() {
    return {
      username: '',
      userphone: '',
      result: 0,
      replace: false,
      message: ''
    }
  },
  methods: {
    onClickCommit() {
      if(/^1(3|4|5|6|7|8|9)\d{9}$/.test(this.userphone)){
        JNB.query({
          serverName: 'userServer',
          cls: 'MyDrugStoreInfoModule',
          method: 'optConsigneeForApp',
          params: {
            shipid: 0,
            compid: this.$loadUserInfo().comp.storeId,
            contactname: this.username,
            contactphone: this.userphone,
            cstatus: this.result ? 2 : 0
          },
          callback: (data)=>{
            let result = JSON.parse(data)

            if (result.code === 200) {
              // 更新用户登录状态
              this.replace = this.$route.query.replace
              if(!this.replace){
                this.$router.replace({
                  path: '/manageConsignee'
                })
              }else{
                this.$router.replace({
                  path: '/goodsOrder'
                })
              }
              this.$toast.success('保存成功')
            }else {
              this.$toast.fail(result.message)
            }
          }
        })
      }else{
        this.message = '手机号格式错误！'
      }
    },
    toggle() {
      this.result = !this.result
    }
  }
}
</script>

